.icon {
  &-flowers {
    position: absolute;
    left: 11px;
    bottom: 11px;
    width: 37px;
    height: 37px;
    border-radius: 50%;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.15);
    border: 1px solid #4f7f0d;
    background-color: #fff;
    background-image: url('~@/assets/images/icons/flowers.png');
    background-position: center;
    background-repeat: no-repeat;
  }

  &-percent {
    position: absolute;
    right: 11px;
    bottom: 11px;
    width: 37px;
    height: 37px;
    border-radius: 50%;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.15);
    border: 1px solid #fbc000;
    background-color: #ffffff;
    background-image: url('~@/assets/images/icons/percent.png');
    background-position: center;
    background-repeat: no-repeat;
  }

  &-like {
    position: absolute;
    left: 11px;
    top: 11px;
    width: 37px;
    height: 37px;
    border-radius: 50%;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.15);
    border: 1px solid #fb9300;
    background-color: #ffffff;
    background-image: url('~@/assets/images/icons/like.png');
    background-position: center;
    background-repeat: no-repeat;
  }

  &-token {
    position: absolute;
    right: 11px;
    bottom: 11px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 39px;
    border-radius: 50%;
    box-shadow: 0 0 21px rgba(0, 0, 0, 0.14);
    background-image: url('~@/assets/images/icons/token.png');
    background-position: center;
    background-repeat: no-repeat;
    color: #573c01;
    font-weight: 700;
  }

  &-percent + &-token {
    right: 55px;
  }
}