.header {
    padding-top: 54px;

    &__top {
        display: flex;
        justify-content: center;
        width: 100%;
        background-color: rgba(243,243,243, 0.8);
        position: fixed;
        top: 0;
        left: 0;
        z-index: 10;

        &_scroll {
            background-color: rgba(243,243,243, 1);

            .logo-scroll {
                display: flex;
                justify-content: center;
            }

            .scroll-hidden {
                display: none;
            }

            .location-settings {
                display: flex;
                flex-direction: row;
                flex-wrap: nowrap;
                justify-content: space-around;
                width: 25%;
                flex-basis: 25%;
            }

            .delivery-city {
                margin-right: 0;
            }

            .fast-links {
                width: 25%;
                flex-basis: 25%;
            }

            .fast-links-list {
                margin: auto;
                justify-content: space-around;
            }

            .user {
                width: calc(16.6666% + 25px);
                flex-basis: calc(16.6666% + 25px);
            }

            .cart {
                width: calc(16.6666% - 25px);
                flex-basis: calc(16.6666% - 25px);
            }
        }

        @media (max-width: 768px) {
            justify-content: space-between;
        }
    }

    &__middle {
        @include container;
        @include row;

        margin-bottom: 10px;
        align-items: center;

        @media (max-width: 768px) {
            display: none;
        }
    }
}