.footer-social {
  .bottom-thumbnails li {
    display: inline-flex;
    margin: 0 7px;
  }

  .bottom-thumbnails a {
    display: inline-block;
    width: 27px;
    height: 27px;

    &:hover {
      img {
        transform: scale(1.2)
      }
    }
  }

  img {
    width: 100%;
    height: 100%;
  }
}