.location-filter {
    @include row;

    width: 58.572%;
    flex-basis: 58.572%;
    margin-bottom: 7px;

    &__item {
        color: #515151;

        &_active {
            color: #7e9d51;
        }

        &:hover {
            color: #789d44;
            text-shadow: 0 0 1px #789d44;
        }
    }
}