.bottom-heading {
  margin-top: 0;
  margin-bottom: 20px;
  color: #515151;
  font-size: 16px;
  font-weight: 700;

  a {
    position: relative;
    margin-left: 7px;
    color: #7e9d51;
    font-size: 14px;
    font-weight: 400;
    border-bottom: 1px solid #7e9d51;
    transition: all ease 0.2s;

    &:hover {
      color: #515151;
      border-bottom: 1px solid #515151;
    }

    i {
      position: absolute;
      right: -12px;
      bottom: 0;
    }
  }
}