.review {
  margin-top: 49px;
  margin-bottom: 14px;
  
  &__heading {
    margin-bottom: 30px;
    font-size: 20px;
    font-weight: 400;
    color: #222222;
    letter-spacing: 0.4px;

    a {
      margin-left: 10px;
      color: #7e9d51;
      font-size: 16px;
      font-weight: 600;

      &:hover {
        color: #515151;
      }
    }
  }

  &__row {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  &__column {
    width: calc(50% - 14px);
    flex-basis: calc(50% - 14px);
  }
}