.crop-text {
  position: relative;
  transition: all ease 0.5s;

  &__button {
    padding-left: 30px;
    margin-left: -30px;
    z-index: 3;
    cursor: pointer;
    background: rgba(237,237,237,0.34);
    background: linear-gradient(to right, rgba(237,237,237,0.34) 0%, rgba(246,246,246,0.7) 12%, rgba(255,255,255,1) 22%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ededed', endColorstr='#ffffff', GradientType=1 );
    &.open {
      position: absolute;
      right: 0;
      bottom: 0;
    }
  }

  span {
    display: block;
  }
}