.scroll-top {
  position: fixed;
  bottom: 10%;
  left: 5%;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.3);
  background-color: #ffffff;
  font-size: 30px;
  color: #8fb35b;
  visibility: hidden;
  opacity: 0;
  z-index: -15;
  transition: all ease 0.3s;

  &_visible {
    visibility: visible;
    opacity: 1;
    z-index: 15;
  }

  &:hover {
    background-color: #8fb35b;
    color: #fff;
  }
}