.category-navigation {
    @include row;

    width: 72.054%;
    flex-basis: 72.054%;
    align-items: stretch;

    &__item {
        position: relative;

        &_active {
            &::after {
                content: '';
                position: absolute;
                right: 0;
                left: 0;
                bottom: 0;
                border-bottom: 2px solid #8fb35b;
            }
        }
    }

    &__link {
        position: relative;
        display: flex;
        align-items: center;
        padding: 12px 0 8px 0;
        color: #4f4f4f;
        font-weight: 700;

        &:hover, &_action {
            color: #7e9d51;
        }

        &_action:hover {
            color: #fbc000;
        }

        .fa-angle-down {
            margin-left: 5px;
        }
    }

    &__link:hover + &__dropdown {
        display: block;
    }

    &__dropdown {
        display: none;
        position: absolute;
        top: 100%;
        left: 0;
        z-index: 10;
        padding-top: 18px;
        margin-top: -8px;

        &:hover {
            display: block;
        }
    }

    &__dropdown-wrapper {
        position: relative;
        display: flex;
        flex-direction: column;
        padding: 16px 49px 20px 25px;
        border: 1px solid #dcdcdc;
        background-color: #f6f6f6;

        &::before, &::after {
            content: '';
            position: absolute;
            width: 0;
            height: 0;
            border-style: solid;
        }

        &::before {
            left: 13px;
            top: -10px;
            z-index: 1;
            border-width: 0 10.5px 10px 10.5px;
            border-color: transparent transparent #f6f6f6 transparent;  
        }
        
        &::after {
            left: 12px;
            top: -11px;
            z-index: 0;
            border-width: 0 11.5px 11px 11.5px;
            border-color: transparent transparent #dcdcdc transparent;
        }
    }

    &__dropdown-link {
        color: #929292;
        font-size: 16px;
        line-height: 33px;
        white-space: nowrap;

        &:hover, &_active {
            color: #333333;
        }

        &_active {
            position: relative;

            &::before {
                content: '';
                position: absolute;
                left: -9px;
                top: 50%;
                margin-top: -3.5px;
                width: 7px;
                height: 7px;
                border-radius: 50%;
                background-color: #8fb35b;
            }
        }
    }
}