.location-settings {
    @include width(31.755%);

    position: relative;
    padding-left: 23px;

    &::before {
        content: '';
        position: absolute;
        bottom: 4px;
        left: 0;
        width: 14px;
        height: 19px;
        background-position: center;
        background-repeat: no-repeat;
        background-size: contain;
        background-image: url('~@/assets/images/icons/pin.png');
    }
}