.city-modal {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 533px;
  padding: 47px 0 39px 0;

  &__heading {
    margin-top: 0;
    margin-bottom: 23px;
    text-align: center;
    color: #515151;
    font-size: 30px;
    font-weight: 400;
  }

  &__field {
    display: flex;
    flex-direction: row;
    align-items: center;
    position: relative;
    width: 324px;
    height: 30px;
    margin-bottom: 21px;
    border: 1px solid #fff;
    border-radius: 3px;
    box-shadow: 1px 1px 6px 1px rgba(0, 0, 0, 0.14);
    background-color: #fff;
    overflow: hidden;
    
    input {
      position: relative;
      width: 100%;
      height: 100%;
      padding: 0 21px;
      margin-right: -25px;
      line-height: 30px;
      color: #515151;
      font-size: 16px;
      border: none;
      outline: none;
      z-index: 1;
    }

    i {
      position: relative;
      z-index: 3;
      margin-right: 11px;
      color: #4f4f4f;
      font-size: 16px;
      cursor: pointer;
    }

    &_drop {
      overflow: visible;
    }
  }

  &__submit {
    margin-top: 32px;
    padding: 10px 50px;
    border-radius: 3px;
    border: none;
    outline: none;
    color: #fff;
    font-size: 14px;
    font-weight: 400;
    background-color: #8fb35b;
    cursor: pointer;
  }
}