@import "variables";

@font-face {
  font-family: '#{$icomoon-font-family}';
  src:  url('#{$icomoon-font-path}/#{$icomoon-font-family}.eot?n22w1o');
  src:  url('#{$icomoon-font-path}/#{$icomoon-font-family}.eot?n22w1o#iefix') format('embedded-opentype'),
    url('#{$icomoon-font-path}/#{$icomoon-font-family}.ttf?n22w1o') format('truetype'),
    url('#{$icomoon-font-path}/#{$icomoon-font-family}.woff?n22w1o') format('woff'),
    url('#{$icomoon-font-path}/#{$icomoon-font-family}.svg?n22w1o##{$icomoon-font-family}') format('svg');
  font-weight: normal;
  font-style: normal;
}

i {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: '#{$icomoon-font-family}';
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-close {
  &:before {
    content: $icon-close; 
  }
}
.icon-next {
  &:before {
    content: $icon-next; 
  }
}
.icon-prev {
  &:before {
    content: $icon-prev; 
  }
}
.icon-icon-search {
  &:before {
    content: $icon-icon-search; 
  }
}
.icon-user {
  &:before {
    content: $icon-user; 
  }
}
.icon-cart {
  &:before {
    content: $icon-cart; 
  }
}

