.messengers {
    margin-top: -4px;
    margin-right: 8px;

    &__list {
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        justify-content: space-between;
    }

    &__link {
        margin-left: 3px;
        line-height: 0;

        &:first-child {
            margin-left: 0;
        }
    }

    &__description {
        display: block;
        margin-top: 6px;
        font-size: 12px;
    }

    &:hover {
        cursor: pointer;
        
        .viber {
            background-image: url('~@/assets/images/icons/viber-active.png');
        }
    
        .telegram {
            background-image: url('~@/assets/images/icons/telegram-active.png');
        }
    
        .whatsapp {
            background-image: url('~@/assets/images/icons/whatsapp-active.png'); 
        }

        .messengers__description {
            color: #7e9d51;
        }
    }  
}