.awards {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: flex-start;
    margin-top: 9px;
    padding-right: 23px;

    &__logo, &__heading, &__link, &__list {
        flex-shrink: 0;
    }

    &__logo {
        width: 65px;
        height: 49px;
        margin-right: 9px;
        background-image: url('~@/assets/images/icons/laurels.png');
        background-repeat: no-repeat;
        background-size: contain;
        background-position: center;

        &:hover {
            background-image: url('~@/assets/images/icons/laurels-active.png'); 
        }
    }

    &__list {
        display: flex;
        flex-direction: column;
        flex-wrap: nowrap;
    }

    &__heading, &__link {
        color: #515151;

        &:hover {
            color: #7e9d51;
        }
    }

    &__heading {
        margin-top: -3px;
        margin-bottom: 6px;
        line-height: 17px;
    }

    &__link {
        margin-bottom: 5px;
        font-size: 12px;
    }
}