.city-list {
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  list-style: none;
  padding: 7px 11px;
  margin: 0;
  z-index: 0;
  opacity: 0;
  visibility: hidden;
  border-radius: 0 0 3px 3px;
  border: 1px solid #ffffff;
  box-shadow: 1px 1px 6px 1px rgba(0, 0, 0, 0.14);
  background-color: #fff;
  transition: all ease 0.3s;

  &__item {
    margin-bottom: 9px;
    color: #515151;
    font-size: 16px;
    cursor: pointer;
  }

  &__opened {
    opacity: 1;
    visibility: visible;
    z-index: 3;
  }
}