.banner {
  position: relative;

  &__item {
    position: relative;
    background: #fff;
    cursor: pointer;

    img {
      width: 100%;
    }
  }

  &__button {
    position: absolute;
    bottom: 91px;
    left: calc(50% - 134.5px);
    padding: 11.5px 48px;
    background-image: linear-gradient(to top, #d39399 0%, #f1b9aa 100%);
    color: #fff;
    font-size: 17px;
  }

  .glide__arrows {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: 0 -22.5px;
    z-index: 0;
  }

  .glide__arrow {
    padding: 0 10px;
    background: none;
    border: none;
    outline: none;
    font-size: 36px;
    color: #8fb35b;
    cursor: pointer;
  }

  .glide__bullets {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 13px;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: center;
    z-index: 3;
  }

  .glide__bullet {
    display: inline-block;
    margin: 0 3px;
    padding: 0;
    width: 13px;
    height: 13px;
    box-shadow: 0 0 13px rgba(0, 0, 0, 0.1);
    border: 2px solid #8fb35b;
    border-radius: 50%;
    outline: none;
    background-color: transparent;
    cursor: pointer;
  }

  .glide__bullet--active {
    background-color:#8fb35b;
  }

  .glide__track {
    position: relative;
    z-index: 1;
  }
}