.review-item {
  width: calc(50% - 18px);
  flex-basis: calc(50% - 18px);
  position: relative;

  &__name {
    margin-right: 4px;
  }

  &__text {
    margin-top: 14px;
    margin-bottom: 34px;
    line-height: 25.04px;
  }

  &__modal {
    position: relative;
    width: 731px;
    padding: 39px 64px 36px 50px;
    box-shadow: 1px 1px 6px 1px rgba(0, 0, 0, 0.1);
    background-color: #fff;
    border-radius: 3px;
    
    .review-item__name {
      color: #515151;
      font-size: 25px;
      font-weight: 400;
    }

    .review-item__date {
      color: #515151;
      font-family: "Open Sans";
      font-size: 16px;
    }

    .review-item__text {
      margin-top: 29px;
      margin-bottom: 0;
      color: #515151;
      font-size: 16px;
    }

    .icon-close {
      position: absolute;
      top: 20px;
      right: 20px;
      cursor: pointer;
    }
  }
}