.category-tabs {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;

  &__item {
    display: block;
    margin-bottom: 16px;
    padding-left: 0;
    margin-left: 0;
    box-shadow: 0 0 15px 1px rgba(0, 0, 0, 0.09);
    background-color: #fff;
    transition: all ease 0.3s;

    &:last-child {
      margin-bottom: 0;
    }

    &_active, &:hover {
      padding-left: 10px;
      margin-left: -10px;
    }

    img {
      width: 100%;
    }
  }
}