.seo-text {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: flex-start;
  padding-left: 80px;
  margin-bottom: 52px;

  &__image {
    height: 100%;
    margin-top: 11px;
    margin-right: 78px;
  }

  &__heading {
    margin-top: 0;
    margin-bottom: 17px;
    font-weight: 400;
    color: #515151;
    font-size: 20px;
    letter-spacing: 0.4px;
  }

  &__content {
    flex-basis: 90%;
    width: 90%;
    color: #515151;
    font-size: 16px;
    line-height: 25.05px;
  }
}