.category-list {
  margin-bottom: 35px;
  
  [data-glide-el="controls"] {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: 0 -22.5px;
    z-index: 0;
  }

  [data-glide-dir] {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 0;
    background: none;
    border: none;
    outline: none;
    font-size: 36px;
    color: #8fb35b;
    cursor: pointer;

    i {
      margin-bottom: -16px;
    }
  }

  .glide__track {
    position: relative;
    z-index: 1;
  }

  .glide__slide {
    height: auto;
  }

  &__item {
    position: relative;
    height: 100%;
    padding-bottom: 35px;

    &-image {
      position: relative;
      display: flex;
      width: 100%;
      box-shadow: 0 0 15px 1px rgba(0, 0, 0, 0.1);
      border-radius: 3px;
      overflow: hidden;

      img {
        width: 100%;
      }

      &:before {
        content: '';
      }
    }

    &-link {
      display: block;
    }

    &-title {
      color: #515151;
      font-size: 16px;
      line-height: 25px;
    }

    &-price {
      color: #515151;
      font-size: 20px;
    }

    &-bottom {
      display: flex;
      flex-direction: row;
      flex-wrap: nowrap;
      justify-content: space-between;
      align-items: center;
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
    }
  }
}