.subscribe-panel {
  padding: 8px 13px 12px 21px;
  margin-bottom: 5px;
  border-radius: 3px;
  background-color: #fff;

  &__description {
    position: relative;

    &_store {
      padding-right: 140px;
    }
  }

  &__store {
    position: absolute;
    top: 0;
    right: 0;
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;

    a {
      margin-bottom: 3px;
    }
  }

  &__heading {
    display: block;
    margin-top: 12px;
    color: #7e9d51;
    font-size: 18px;
  }

  &__text {
    margin: 14px 0 21px 0;
    color: #515151;
  }

  &__submit {
    @include row;

    align-items: center;
  }

  &__field {
    margin-right: 16px;
    color: #969696;
    line-height: 36.6px;
    border: none;
    outline: none;
    box-shadow: none;
    border-bottom: 1px solid #dbdada;
  }

  &__button {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 129px;
    height: 35px;
    border-radius: 3px;
    border: none;
    outline: none;
    box-shadow: none;
    background-color: #8fb35b;
    color: #fff;
    font-size: 13px;
    cursor: pointer;

    &:hover {
      background-color: #fbc000;
    }
  }
}