.phone-block {
    margin-top: -3px;
    font-size: 20px;
    line-height: 29px;

    &__phone {
        color: #515151;
        font-size: 20px;
    }

    &__description {
        display: block;
        font-size: 12px;
        line-height: 17px;

        a {
            text-decoration: underline;
        }

        a:hover {
            text-decoration: none;
        }
    }
}