.backdrop {
  display: flex;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: -20;
  justify-content: center;
  align-items: center;
  border: 1px solid #ffffff;
  visibility: hidden;
  background-color:rgba(173, 173, 173, 0.5);
  opacity: 0;
  transition: all ease 0.3s;

  &_opened {
    visibility: visible;
    z-index: 20;
    opacity: 1;
  }
}