@mixin container {
    width: 1170px;
    padding: 0 15px;
    margin: auto;

    @media (max-width: 768px) {
        width: 750px;
    }
}

@mixin row {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;

    > * {
        flex-shrink: 0;
    }
}

@mixin width($property) {
    flex-basis: $property;
    width: $property;
}