.category-panel {
  width: 64.7%;
  flex-basis: 64.7%;
  flex-shrink: 0;

  &__text {
    margin-bottom: 36px;
    color: #573b3b;
    font-size: 16px;
    line-height: 29.99px;

    strong {
      font-weight: 600;
    }
  }

  &__button {
    padding: 10.5px 69px;
    border-radius: 3px;
    background: none;
    border: 1px solid;
    color: #515151;
    font-size: 20px;
    cursor: pointer;
    transition: all ease 0.2s;

    &:hover {
      color: #fff;
    }

    &_pink {
      border-color: #e97486;

      &:hover {
        
        background: #fda1bb;
      }
    }

    &_green {
      border-color: #395906;

      &:hover {
        background: #5e6c38;
      }
    }

    &_blue {
      border-color: #3793d0;

      &:hover {
        background: #46a7d8;
      }
    }
  }
}