.category-preview {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  margin-bottom: 39px;

  &__content {
    position: relative;
    width: calc(100% - 29.3% - 47px);
    flex-basis: calc(100% - 29.3% - 47px);
  }

  &__aside {
    width: 29.3%;
    flex-basis: 29.3%;
  }
}