.main-carousel {
    margin-bottom: 10px;

    .glide__bullets {
        position: absolute;
        left: 0;
        right: 0;
        bottom: 13px;
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        justify-content: center;
        z-index: 3;
    }

    .glide__bullet {
        display: inline-block;
        margin: 0 3px;
        padding: 0;
        width: 13px;
        height: 13px;
        box-shadow: 0 0 13px rgba(0, 0, 0, 0.31);
        border: 2px solid #ffffff;
        border-radius: 50%;
        outline: none;
        background-color: transparent;
        cursor: pointer;
    }

    .glide__bullet--active {
        background-color: #fff;
    }

    .glide__arrows {
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        justify-content: space-between;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
    }

    .glide__arrow {
        padding: 0 10px;
        background: none;
        border: none;
        outline: none;
        font-size: 36px;
        color: #fff;
        cursor: pointer;
    }
}