.address {
  width: 100%;
  flex-basis: 100%;
  flex-shrink: 0;
  margin-top: 32px;
  margin-bottom: 30px;

  &__item {
    display: block;
    color: #969696;
    font-size: 12px;
    line-height: 18px;

    a {
      position: relative;
      color: #7e9d51;
      font-family: "Open Sans";
      border-bottom: 1px solid #7e9d51;
      transition: all ease 0.2s;

      &:hover {
        color: #515151;
        border-bottom: 1px solid #515151;
      }

      i {
        position: absolute;
        right: -12px;
        bottom: 1px;
      }
    }
  }
}