.footer {
  padding-top: 14px;
  background-color: rgba(243, 243, 243, 0.8);
  border-top: 1px solid #dbdada;

  &::after {
    display: block;
    content: "";
    clear: both;
  }

  &__content {
    width: 1170px;
    padding: 0 15px;
    margin: auto;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: center;

    &_border {
      border-bottom: 1px solid #dbdada;
    }

    &_baseline {
      flex-wrap: wrap;
      padding-top: 29px;
      align-items: flex-start;
    }
  }
}