.price-filter {
    @include row;

    align-items: center;
    margin: 0 -10px;
    
    &__item {
        padding: 0 10px;
    }

    &__value {
        display: inline-flex;
        align-items: center;
        color: #8fb35b;
        line-height: 17px;

        &_active {
            padding: 5px 12px;
            border-radius: 3px;
            color: #fff;
            background-color: #8fb35b;

            i {
                margin-left: 6px;
                font-size: 9px;
            }
        }
    }
}